html, body {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  min-width: 320px; }

a:hover {
  text-decoration: none; }

.sidebar {
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0;
  margin: 0;
  background: white;
  background: -moz-linear-gradient(top, #ffffff 0%, #ebf1f4 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, #ffffff), color-stop(100%, #ebf1f4));
  background: -webkit-linear-gradient(top, #ffffff 0%, #ebf1f4 100%);
  background: -o-linear-gradient(top, #ffffff 0%, #ebf1f4 100%);
  background: -ms-linear-gradient(top, #ffffff 0%, #ebf1f4 100%);
  background: linear-gradient(to bottom, #ffffff 0%, #ebf1f4 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#ebf1f4', GradientType=0); }
  @media (max-width: 768px) {
    .sidebar {
      height: auto;
      position: relative; } }

.navbar {
  border: none;
  margin: 0; }
  .navbar .navbar-header {
    width: 100%; }

.logo_block {
  overflow: hidden; }

.logo_wrap {
  margin: 24px 0;
  overflow: hidden; }
  @media (max-width: 768px) {
    .logo_wrap {
      margin: 16px 0 16px; } }

.brand-link {
  display: block;
  position: relative; }
  .brand-link img {
    margin: 0 auto; }
  .brand-link:after, .brand-link:before {
    content: '';
    width: calc(50% - 90px);
    margin: 0 20px;
    height: 1px;
    background: #000000;
    display: block;
    position: absolute;
    top: 50%;
    left: 0;
    transition: all 150ms; }
    @media (max-width: 992px) {
      .brand-link:after, .brand-link:before {
        width: calc(50% - 70px);
        margin: 0 10px; } }
    @media (max-width: 768px) {
      .brand-link:after, .brand-link:before {
        width: calc(50% - 140px);
        margin: 0 70px; } }
  .brand-link:after {
    left: auto;
    right: 0; }

.logo_sign {
  display: block;
  width: 140px;
  text-align: center;
  margin: 0 auto;
  font-size: 10px;
  color: #bdc0c5;
  text-transform: uppercase; }
  @media (max-width: 768px) {
    .logo_sign {
      width: 100%;
      margin: 0 auto 32px; } }

.navbar-toggle {
  position: absolute;
  top: 0;
  right: -8px;
  z-index: 999;
  border-color: #4c596e; }
  .navbar-toggle .icon-bar {
    background-color: #4c596e; }

#main-navbar {
  padding: 0;
  margin: 100px 0 0;
  max-height: 100%;
  position: relative;
  z-index: 20; }
  @media (max-width: 768px) {
    #main-navbar {
      margin: 0 auto 48px; } }
  @media (max-width: 768px) {
    #main-navbar .navbar-nav {
      margin: 0; } }
  #main-navbar .navbar-nav > li {
    width: 100%;
    text-align: center;
    text-transform: uppercase; }
    #main-navbar .navbar-nav > li a {
      color: #bdc0c5;
      font-weight: 300;
      font-size: 22px;
      transition: all 150ms; }
      @media (max-width: 992px) {
        #main-navbar .navbar-nav > li a {
          font-size: 18px; } }
      #main-navbar .navbar-nav > li a:hover, #main-navbar .navbar-nav > li a:focus {
        background: none;
        color: #162022;
        font-weight: 600; }
    #main-navbar .navbar-nav > li.active {
      border-right: 2px solid;
      border-left: 2px solid;
      border-color: #4c596e; }
      #main-navbar .navbar-nav > li.active a {
        color: #162022;
        font-weight: 600; }

.sidebar_footer {
  position: absolute;
  z-index: 10;
  bottom: 0;
  left: 0;
  width: 100%;
  height: auto; }
  @media (max-width: 768px) {
    .sidebar_footer {
      height: 100%; } }

.callback_block {
  display: block;
  width: 100%;
  position: relative;
  text-align: center;
  left: auto;
  bottom: auto; }
  @media (max-width: 768px) {
    .callback_block {
      position: absolute;
      bottom: 4px;
      left: 0; } }

.callback_block_title {
  font-size: 16px;
  position: relative;
  color: #4c596c;
  text-align: center;
  display: block;
  margin-bottom: 30px;
  transition: all 150ms; }
  @media (max-width: 992px) {
    .callback_block_title {
      font-size: 14px; } }
  @media (max-width: 768px) {
    .callback_block_title {
      display: inline-block;
      vertical-align: middle;
      margin-bottom: 0; } }
  @media (max-width: 480px) {
    .callback_block_title {
      font-size: 12px; } }
  .callback_block_title:after, .callback_block_title:before {
    content: '';
    width: calc(50% - 90px);
    margin: 0 10px;
    height: 1px;
    background: #000000;
    display: block;
    position: absolute;
    top: 50%;
    left: 0;
    transition: all 150ms; }
    @media (max-width: 992px) {
      .callback_block_title:after, .callback_block_title:before {
        width: calc(50% - 90px);
        margin: 0 10px; } }
    @media (max-width: 768px) {
      .callback_block_title:after, .callback_block_title:before {
        display: none; } }
  .callback_block_title:after {
    left: auto;
    right: 0; }

.callback_block_phone {
  font-size: 22px;
  font-weight: 700;
  color: #162022;
  margin-bottom: 10px;
  transition: all 150ms; }
  @media (max-width: 992px) {
    .callback_block_phone {
      font-size: 20px; } }
  @media (max-width: 768px) {
    .callback_block_phone {
      margin-bottom: 0;
      display: inline-block;
      vertical-align: middle; } }
  @media (max-width: 480px) {
    .callback_block_phone {
      font-size: 16px; } }

.exit_block {
  width: 100%;
  display: block;
  text-align: center;
  padding: 50px 0; }
  @media (max-width: 768px) {
    .exit_block {
      padding: 0;
      position: absolute;
      top: 8px;
      left: 0;
      width: 100px; } }

.btn-exit {
  width: 80%;
  background-color: #162022;
  color: #fff;
  font-weight: 600;
  font-size: 16px; }
  .btn-exit:hover {
    color: #fff;
    background-color: #202f31; }

.content {
  padding: 0;
  box-sizing: border-box; }

.content_title_wrapper {
  background: #4c596c;
  overflow: hidden;
  padding: 60px;
  transition: all 150ms; }
  @media (max-width: 992px) {
    .content_title_wrapper {
      padding: 40px; } }
  @media (max-width: 768px) {
    .content_title_wrapper {
      padding: 20px; } }

.content_title {
  color: #fff;
  text-transform: uppercase;
  margin: 0;
  font-weight: 700;
  font-size: 32px;
  display: inline-block; }
  .content_title span {
    display: table-cell;
    vertical-align: top; }
    .content_title span:first-of-type {
      padding: 11px 10px 11px 0; }
    .content_title span h1 {
      margin: 0; }

.btn-back_to_site {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  border: 1px solid #fdfefe;
  padding: 7px;
  display: inline-block;
  margin: 0;
  color: #fff; }
  .btn-back_to_site:hover {
    color: #fff; }
  .btn-back_to_site span {
    margin: 0 8px;
    transition: all 150ms; }
    .btn-back_to_site span:hover {
      border-bottom: 1px dotted #fff; }
  @media (max-width: 768px) {
    .btn-back_to_site {
      margin-top: 20px; } }

.content_container {
  padding: 50px 45px;
  box-sizing: border-box; }

.edit_btn, .delete_btn {
  background: none;
  border: none;
  float: right;
  padding: 4px 0;
  margin: 0 0 0 4px; }

.icon_edit {
  color: #5fc0dc;
  margin: 0 8px;
  transition: color 300ms; }
  .icon_edit:hover {
    color: #4ab8d7; }

.icon_delete {
  color: #d75553;
  margin: 0 8px;
  transition: color 300ms; }
  .icon_delete:hover {
    color: #d2413e; }

.table_wrapper {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  border: 1px solid #dddddd;
  padding: 30px 15px; }

.table {
  margin: 0; }

#dropZone {
  margin: 36px 0;
  text-align: center;
  color: #5fc0dc;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  border: 1px dashed #5fc0dc;
  padding: 40px;
  cursor: pointer; }
  #dropZone form {
    margin: 0; }

#dropZone.hover {
  background: #ddd;
  border-color: #aaa; }

#dropZone.error {
  background: #faa;
  border-color: #f00; }

#dropZone.drop {
  background: #afa;
  border-color: #0f0; }

.message_form {
  margin-bottom: 50px;
  overflow: hidden; }

.message_form-title, .message_form-text {
  display: inline-block;
  vertical-align: top;
  margin: 0; }
  @media (max-width: 768px) {
    .message_form-title, .message_form-text {
      margin-bottom: 32px; } }

.message_form-text {
  width: 100%;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  resize: none;
  padding: 10px; }

.submit_message_form {
  background: #5db85c;
  color: #fff;
  transition: all 150ms;
  display: inline-block;
  vertical-align: top; }
  .submit_message_form:hover {
    color: #fff;
    background: #459d44; }

.mess_list {
  list-style: none;
  margin: 0;
  padding: 0; }
  .mess_list li {
    border-top: 1px solid #dfdfdf;
    padding: 32px 0 0; }
    .mess_list li .mess-date, .mess_list li .mess-time {
      color: #aaaaaa;
      transition: all 150ms;
      font-weight: 600;
      font-size: 12px; }
    .mess_list li .mess-time {
      margin-bottom: 10px; }
    .mess_list li .mess-heading {
      transition: all 150ms;
      font-weight: 600;
      font-size: 16px;
      margin: 0 0 5px; }
    .mess_list li:hover .mess-date, .mess_list li:hover .mess-time, .mess_list li:hover .mess-heading {
      color: #5fc0dc; }

